.vuetable-empty-result {
  font-weight: bold;
}
.vuetable-pagination {
  display: flex;
  flex-direction: row;
  font-weight: bold;
  justify-content: space-between;
}
.pagination-info {
  font-weight: bold;
}
.pagination .btn-nav {
  width: 30px;
  height: 35px;
}
.pagination .page {
  cursor: pointer;
}
.pagination a.btn-nav.disabled {
  color: lightgray;
  border: 1px solid lightgray;
  border-radius: 3px;
  padding: 5px 10px;
  cursor: not-allowed;
  font-weight: bold;
  color: #000;
}
.cust-label {
  font-weight: bold;
  margin-right: 20px;
}
.w10 {
  width: 7% !important;
  margin: 0px 10px 0px 10px;
}
.f-right {
  margin-right: -10px !important;
}
.cust-form-control {
  width: 20%;
  max-width: 200px !important;
  min-width: 200px !important;
  border-radius: 0px;
  background-color: inherit;
}